// Setting Listeners when a button is clicked for Test LeveL and Test Type:
document.addEventListener('DOMContentLoaded', function() {
  document.getElementById('LVL1-TestBtn').addEventListener('click', function() {
    setTestLVL('LVL 1');
  });
  document.getElementById('LVL2-TestBtn').addEventListener('click', function() {
    setTestLVL('LVL 2');
  });
  document.getElementById('LVL3-TestBtn').addEventListener('click', function() {
    setTestLVL('LVL 3');
  });

  // Add similar event listeners for test type buttons
  document.getElementById('Direct-TestBtn').addEventListener('click', function() {
    setTestType('Direct');
  });
  document.getElementById('SmallFreinds-TestBtn').addEventListener('click', function() {
    setTestType('SmallFriends');
  });
  document.getElementById('BigFriends-TestBtn').addEventListener('click', function() {
    setTestType('BigFriends');
  });
  document.getElementById('Multiplication-TestBtn').addEventListener('click', function() {
    setTestType('Multiplication');
  });
});

function setTestLVL(testLVLchoice){
 let testLVL;
  switch (testLVLchoice) {
    case 'LVL 1':
      console.log("Test Type has benn set to",testLVLchoice);
      testLVL = 1;
      break;
    case 'LVL 2':
      console.log("Test Type has benn set to",testLVLchoice);
      testLVL = 2;
      break;
    case 'LVL 3':
      console.log("Test Type has benn set to",testLVLchoice);
      testLVL = 3;
      break;
  
    default:
      console.error('Invalid test LeveL');
      return;
  }
  return testLVL;
}
let testLVL=setTestLVL();
function setTestType(testType) {
    let testInstance;
  
    switch (testType) {
      case 'Direct':
        console.log("Test Type has benn set to",testType);
        testInstance = new DirectTest(testLVL);
        break;
      case 'SmallFriends':
        console.log("Test Type has benn set to",testType);
        testInstance = new SmallFriendsTest(testLVL);
        break;
      case 'BigFriends':
        console.log("Test Type has benn set to",testType);
        testInstance = new BigFriendsTest(testLVL);
        break;
      case 'Multiplication':
        console.log("Test Type has benn set to",testType);
        testInstance = new MultiplicationTest(testLVL);
        break;
      default:
        console.error('Invalid test type');
        return;
    }
    return testInstance;
  }
  // Export the Test Obeject:
  const testInstance=setTestType();
